import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import VerifyCode from '../../models/VerifyCode';
import AppNotification from '../../helpers/AppNotification';
import UserModel from '../../models/UserModel';
import Swal from "sweetalert2";

const validationSchema = Yup.object().shape({
    email: Yup.string().required('Khng c  trng!').email('Email phi ng nh dng!'),
    verify_code: Yup.string()
        .matches(/^\d{6}$/, 'M xc thc phi c ng 6 ch s')
});

function ModalUpdateEmail(props) {
    const [initialValues, setInitialValues] = useState({
        email: '',
        verify_code: '',
    });

    const handleChange = (event) => {
        setInitialValues({
            ...initialValues,
            [event.target.name]: event.target.value
        });
    };

    const [modalShowNoti, setModalShowNoti] = React.useState(false);
    const [notication, setNotication] = useState();
    const handleCloseModalNoti = () => {
        setModalShowNoti(false);
    };
    const handleShowModalNoti = () => {
        setModalShowNoti(true);
    };

    const handleSubmit = (values) => {
        UserModel.updateEmail(values).then(function (res) {
            if (res.success == true) {
                Swal.fire({
                    icon: "success",
                    title: "Email  c cp nht!",
                    showConfirmButton: false,
                    timer: 2000,
                });
                setTimeout(() => {
                    reloadPage();
                }, 2000);
            } else {
                Swal.fire({
                    icon: "error",
                    title: res.data,
                    showConfirmButton: false,
                    timer: 2000,
                });
            }
        }).catch(function (err) {
            Swal.fire({
                icon: "error",
                title: 'Email cp nht tht bi!',
                showConfirmButton: false,
                timer: 2000,
            });
            console.log(err);
            AppNotification.showError('Khi Phc Mt Khu Tht Bi!');
        })
    };

    const sendOTP = (email) => {
        VerifyCode.getOTP({
            type: "CHANGEMAIL",
            email: email,
          }).then(function (res) {
            Swal.fire({
                icon: "success",
                title: "M OTP  c gi vo email ca bn!",
                showConfirmButton: false,
                timer: 2000,
            });
            AppNotification.showSuccess('M OTP  c gi vo email ca bn!');
        }).catch(function (err) {
            Swal.fire({
                icon: "error",
                title: "Khng gi c m OTP!",
                showConfirmButton: false,
                timer: 2000,
            });
            console.log(err);
            AppNotification.showError('Ly M OTP Tht Bi!');
        })
    }

    const reloadPage = () => {
        window.location.reload();
    };

    return (
        <>
            <Modal
                {...props}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Header>
                    <Modal.Title id="contained-modal-title-vcenter">
                        Thay i Email
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Formik
                        initialValues={initialValues}
                        validationSchema={validationSchema}
                        onSubmit={handleSubmit}
                        enableReinitialize={true}
                    >
                        <Form>
                            <div className='row'>
                                <div className='col-12'>
                                    <h4 className='text-warning'>Bc 1 : Nhp email mi</h4>
                                    <div className="input-group mb-2">
                                        <Field type="text" onChange={handleChange} placeholder="Nhp email mi..." className="form-control" name="email" />
                                        <button className="btn btn-warning" type="button" onClick={() => sendOTP(initialValues.email)}>
                                            Ly m
                                        </button>
                                    </div>
                                    <ErrorMessage
                                        component="div"
                                        name="email"
                                        className="text-danger"
                                    />
                                </div>
                            </div>
                            <div className='row'>
                                <div className='col-12'>
                                    <h4 className='text-warning'>Bc 2 : Nhp m xc minh</h4>
                                    <div className="input-group mb-2">
                                        <Field type="text" placeholder="Nhp m xc minh..." className="form-control" name="verify_code" />
                                        <button className="btn btn-primary" type="submit">
                                            Cp Nht
                                        </button>
                                    </div>
                                    <ErrorMessage
                                        component="div"
                                        name="verify_code"
                                        className="text-danger"
                                    />
                                </div>
                            </div>
                        </Form>
                    </Formik>
                    <p>Lu :</p>
                    <ul>
                        <li>
                            <span className="text-warning">Bc 1:</span> nhp email mi v nhn nt <span className="text-warning">ly m</span> , S c 1 m OTP c gi vo <span className='text-warning'>email c</span> ca bn
                        </li>
                        <li>
                            <span className="text-warning">Bc 2:</span> <span className='text-primary'>M hp th email c</span>  ly m OTP, sau  nhp m OTP v nhn nt <span className="text-warning">Cp nht</span>
                        </li>
                    </ul>
                </Modal.Body>
                <Modal.Footer>
                    <Button onClick={props.onHide}>ng</Button>
                </Modal.Footer>
            </Modal>
        </>
    );
}

export default ModalUpdateEmail;