import { Field, Form, Formik } from 'formik';
import React from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import * as Yup from 'yup';
import Swal from 'sweetalert2';
import UserBankAcountModel from '../../models/UserBankAcountModel';
import AppNotification from '../../helpers/AppNotification';
import { useDispatch } from 'react-redux';
import { setShowPreloader } from '../../redux/action';


const validationSchema = Yup.object().shape({
    accountNumber: Yup.number()
        .required('Vui lng nhp s ti khon'),
    phone: Yup.string()
    .required('Vui lng nhp s ti khon')
    .test('phone-length', 'S in thoi phi c 10 ch s', (value) => {
        if (value) {
        return value.length === 10;
        }
        return true;
    }),
    password_level_two: Yup.string()
        .required('Vui lng nhp s ti khon')
        .test('password_level_two-length', 'password_level_two phi c 6 ch s', (value) => {
            if (value) {
                return String(value).length === 6;
            }
            return true;
        }),
});

function ModalCreateAcount(props) {
    const dispatch = useDispatch();

    const initialValues = {
        accountNumber: '',
        phone: '',
        password_level_two: '',
        bin: props.bin,
        type: props.type,
        bank_name: props.bankName,
    };
    const handleSubmit = (values) => {
        dispatch(setShowPreloader(true));
        UserBankAcountModel.createUserBankAcount(values).then(function (res) {
            dispatch(setShowPreloader(false));
            if (res.success === true) {
                Swal.fire({
                    icon: "success",
                    title: "Thm ti khon thnh cng!",
                    showConfirmButton: false,
                    timer: 2000,
                });
                setTimeout(() => {
                    props.reloadPage();
                }, 2000);
            } else {
                Swal.fire({
                    icon: "error",
                    title: res.data,
                    showConfirmButton: false,
                    timer: 2000,
                });
            }
        }).catch(function (err) {
            dispatch(setShowPreloader(false));
            Swal.fire({
                icon: "error",
                title: 'Thm ti khon tht bi!',
                showConfirmButton: false,
                timer: 2000,
            });
            console.log(err);
        })
    };
    return (
        <Modal
            {...props}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header>
                <Modal.Title id="contained-modal-title-vcenter">
                    Thm Ti khon {props.bankName}
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Formik
                    initialValues={initialValues}
                    validationSchema={validationSchema}
                    onSubmit={handleSubmit}
                >
                    {({ errors, touched }) => (
                        <Form>
                            <div className='card-body'>
                                <div className="row pt-3">
                                    <div className="col">
                                        <strong>Tnh trng:</strong>
                                    </div>
                                    <div className="col">
                                        {props.BankStatus === 1 ? (
                                            <span className="badge badge-success">Hot ng</span>
                                        ) : (
                                            <span className="badge badge-danger">ang Tt</span>
                                        )}
                                    </div>
                                </div>

                                <div className="row pt-3">
                                    <div className="col">
                                        <strong>ang S Dng:</strong>
                                    </div>
                                    <div className="col">
                                        {props.UserBankStatus === 1 ? (
                                            <span className="badge badge-success">Bt</span>
                                        ) : (
                                            <span className="badge badge-danger">Tt</span>
                                        )}
                                    </div>
                                </div>

                                <div className="row pt-3">
                                    <div className="col">
                                        <strong>Gi m ngn hng:</strong>
                                    </div>
                                    <div className="col">
                                        0  (Vnh Vin)
                                    </div>
                                </div>
                            </div>
                            <div className="mb-3">
                                <label htmlFor="accountNumber" className="form-label">
                                    <span className='text-success'>S ti khon ngn hng {props.bankName}</span> (*)
                                </label>
                                <Field
                                    type="text"
                                    id="accountNumber"
                                    name="accountNumber"
                                    className={`form-control ${errors.accountNumber && touched.accountNumber ? 'is-invalid' : ''
                                        }`}
                                    autoFocus
                                />
                                {errors.accountNumber && touched.accountNumber && (
                                    <div className="invalid-feedback">{errors.accountNumber}</div>
                                )}
                                <label htmlFor="phone" className="form-label">
                                    <span className='text-success'>S in thoi ng k</span> (*)
                                </label>
                                <Field
                                    type="text"
                                    id="phone"
                                    name="phone"
                                    className={`form-control ${errors.phone && touched.phone ? 'is-invalid' : ''
                                        }`}
                                    autoFocus
                                />
                                {errors.phone && touched.phone && (
                                    <div className="invalid-feedback">{errors.phone}</div>
                                )}
                                <label htmlFor="password_level_two" className="form-label">
                                    <span className='text-success'>M 2FA</span> (*)
                                </label>
                                <Field
                                    type="text"
                                    id="password_level_two"
                                    name="password_level_two"
                                    className={`form-control ${errors.password_level_two && touched.password_level_two ? 'is-invalid' : ''
                                        }`}
                                    autoFocus
                                />
                                {errors.password_level_two && touched.password_level_two && (
                                    <div className="invalid-feedback">{errors.password_level_two}</div>
                                )}
                                <p>Lu :</p>
                                <ul>
                                    <li>
                                        - S ti khon<span className="text-warning">phi trng vi s ti khon ngn hng tht.</span>
                                    </li>
                                    <li>
                                        - Bn <span className="text-warning">ch cn nhp S ti khon ngn hng</span>, h thng s t ng tm tn Ch ti khon.
                                    </li>
                                    <li>
                                        - Vic kim tra tn t ng s <span className="text-warning">mt mt khon ph l 2.000 .</span>
                                    </li>
                                    <li>
                                        - H thng <span className="text-warning">khng hon tin khi qu khch nhp sai</span> STK.
                                    </li>
                                </ul>
                            </div>

                            <div className="modal-footer">
                                <button type="button" className="btn btn-danger" data-dismiss="modal" onClick={props.onHide}>
                                    Close
                                </button>
                                <button type="submit" className="btn btn-success">
                                    Xc nhn
                                </button>
                            </div>

                        </Form>
                    )}
                </Formik>
            </Modal.Body>
        </Modal>
    );
}

export default ModalCreateAcount;