import React, { useState } from 'react';
import ThemeBreadcrumb from '../components/themes/ThemeBreadcrumb';
import MasterLayout from '../layouts/MasterLayout';
import ModalChangePassWord from '../components/changepassword/ModalChangePassWord';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import UserModel from '../models/UserModel';
import AppNotification from '../helpers/AppNotification';
import Swal from "sweetalert2";

const changePasswordSchema = Yup.object().shape({
    old_password: Yup.string().required('Vui lng nhp mt khu c'),
    new_password: Yup.string()
        .min(6, ' di ti thiu 6 k t')
        .max(32, ' di ti a 32 k t')
        .matches(/[A-Z]/, 'Phi c t nht mt ch in hoa')
        .required('Vui lng nhp mt khu mi'),
    repeat_password: Yup.string()
        .oneOf([Yup.ref('new_password'), null], 'Mt khu khng khp')
        .required('Vui lng nhp li mt khu'),
});

const changeSecondPasswordSchema = Yup.object().shape({
    old_password: Yup.string().required('Vui lng nhp mt khu c'),
    new_password: Yup.string()
        .min(6, ' di ti thiu 6 k t')
        .max(32, ' di ti a 32 k t')
        .matches(/^[0-9]{6}$/, 'Mt khu phi c 6 k t s')
        .required('Vui lng nhp mt khu mi'),
    repeat_password: Yup.string()
        .oneOf([Yup.ref('new_password'), null], 'Mt khu khng khp')
        .required('Vui lng nhp li mt khu'),
});

function UserChangePasswordPage(props) {
    const [modalShow, setModalShow] = React.useState(false);
    const [restoreTpye, setRestoreTpye] = useState("");
    const [formPassword] = useState({
        old_password: "",
        new_password: "",
        repeat_password: "",
        type: "PASSWORD",
    })

    const [formSecondpass] = useState({
        old_password: "",
        new_password: "",
        repeat_password: "",
        type: "SECONDPASS",
    })
    const handleShowModal = (type) => {
        setModalShow(true)
        setRestoreTpye(type);
    }
    const handleCloseModal = () => {
        setModalShow(false);
    };

    const handleSubmit = (values) => {
        if (values.type == "PASSWORD") {
            UserModel.changePassword(values).then(function (res) {
                if (res.success == true) {
                    Swal.fire({
                        icon: "success",
                        title: "i mt khu thnh cng!",
                        showConfirmButton: false,
                        timer: 2000,
                    });
                    AppNotification.showSuccess('Cp Nht Mt Khu Thnh Cng!');
                }else{
                    Swal.fire({
                        icon: "error",
                        title: res.message,
                        showConfirmButton: false,
                        timer: 2000,
                    });
                }
            }).catch(function (err) {
                console.log(err);
                Swal.fire({
                    icon: "error",
                    title: "i mt khu Tht bi!",
                    showConfirmButton: false,
                    timer: 2000,
                });
                AppNotification.showError('Cp Nht Mt Khu Tht Bi!');
            })
        }
        if (values.type == "SECONDPASS") {
            UserModel.changePassword(values).then(function (res) {
                if (res.success == true) {
                    Swal.fire({
                        icon: "success",
                        title: "i mt khu Cp 2 Thnh Cng!",
                        showConfirmButton: false,
                        timer: 2000,
                    });
                    AppNotification.showSuccess('Cp Nht Mt Khu Cp 2 Thnh Cng!');
                }else {
                    Swal.fire({
                        icon: "error",
                        title: res.data,
                        showConfirmButton: false,
                        timer: 2000,
                    });
                }
            }).catch(function (err) {
                Swal.fire({
                    icon: "error",
                    title: "i mt khu Cp 2 Tht bi!",
                    showConfirmButton: false,
                    timer: 2000,
                });
                console.log(err);
                AppNotification.showError('Cp Nht Mt Khu Tht Bi!');
            })
        }
        
    }
    return (
        <MasterLayout>
            <ThemeBreadcrumb title="i mt khu" />
            <div className='row'>
                <div className='col-xl-12'>
                    <div className='row'>
                        <div className='col-xl-6'>
                            <div className="card">
                                <div className="card-header">
                                    <h4 className="card-title">i mt khu</h4>
                                </div>
                                <div className="card-body p-3">
                                    <Formik
                                        initialValues={formPassword}
                                        validationSchema={changePasswordSchema}
                                        onSubmit={(values) => handleSubmit(values)}
                                    >
                                        {({ errors, touched }) => (
                                            <Form>
                                                <div className="mb-3 row">
                                                    <label className="col-sm-3 col-form-label col-form-label-sm text-primary">
                                                        Mt khu c:
                                                    </label>
                                                    <div className="col-sm-9">
                                                        <Field
                                                            type="password"
                                                            className="form-control form-control-sm"
                                                            name="old_password"
                                                            placeholder="Mt khu c"
                                                        />
                                                        <ErrorMessage
                                                            component="div"
                                                            name="old_password"
                                                            className="text-danger"
                                                        />
                                                    </div>
                                                </div>
                                                <div className="mb-3 row">
                                                    <label className="col-sm-3 col-form-label col-form-label-sm text-primary">
                                                        Mt khu mi:
                                                    </label>
                                                    <div className="col-sm-9">
                                                        <Field
                                                            type="password"
                                                            className="form-control form-control-sm"
                                                            name="new_password"
                                                            placeholder="Mt khu mi"
                                                        />
                                                        <ErrorMessage
                                                            component="div"
                                                            name="new_password"
                                                            className="text-danger"
                                                        />
                                                    </div>
                                                </div>
                                                <div className="mb-3 row">
                                                    <label className="col-sm-3 col-form-label col-form-label-sm text-primary">
                                                        Nhp li mt khu:
                                                    </label>
                                                    <div className="col-sm-9">
                                                        <Field
                                                            type="password"
                                                            className="form-control form-control-sm"
                                                            name="repeat_password"
                                                            placeholder="Nhp li mt khu"
                                                        />
                                                        <ErrorMessage
                                                            component="div"
                                                            name="repeat_password"
                                                            className="text-danger"
                                                        />
                                                        <input
                                                            type="hidden"
                                                            className="form-control form-control-sm"
                                                            name="type"
                                                            value="PASSWORD"
                                                        />
                                                    </div>
                                                </div>
                                                <div className="row">
                                                    <div className="col-xl-12 col-xxl-12">
                                                        <div className="mb-2">
                                                            <h4 className="card-title card-intro-title text-warning">
                                                                Yu cu mt khu
                                                            </h4>
                                                        </div>
                                                        <div className="card-content">
                                                            <ul className="text-success">
                                                                <li> di ti thiu 6 k t ti a 32 k t</li>
                                                                <li>C t nht mt ch vit hoa</li>
                                                                <li>Dng  ng nhp vo h thng qun l v ngn hng</li>
                                                            </ul>
                                                        </div>
                                                        <button type="submit" className="btn btn-success btn-sm mt-3">
                                                            Cp Nht
                                                        </button>
                                                        <button
                                                            type="button"
                                                            className="btn btn-outline-info btn-sm mt-3 ms-2"
                                                            onClick={(type) => handleShowModal("PASSWORD")}
                                                        >
                                                            Khi Phc
                                                        </button>
                                                    </div>
                                                </div>
                                            </Form>
                                        )}
                                    </Formik>
                                </div>
                            </div>
                        </div>
                        <div className='col-xl-6'>
                            <div className='row'>
                                <div className='col-xl-12'>
                                    <div className="card">
                                        <div className="card-header">
                                            <h4 className="card-title">i mt khu cp 2</h4>
                                        </div>
                                        <div className="card-body p-3">
                                            <Formik
                                                initialValues={formSecondpass}
                                                validationSchema={changeSecondPasswordSchema}
                                                onSubmit={(values) => handleSubmit(values)}
                                            >
                                                {({ errors, touched }) => (
                                                    <Form>
                                                        <div className="mb-3 row">
                                                            <label className="col-sm-3 col-form-label col-form-label-sm text-primary">
                                                                Mt khu c:
                                                            </label>
                                                            <div className="col-sm-9">
                                                                <Field
                                                                    type="password"
                                                                    className="form-control form-control-sm"
                                                                    name="old_password"
                                                                    placeholder="Mt khu c"
                                                                />
                                                                <ErrorMessage
                                                                    component="div"
                                                                    name="old_password"
                                                                    className="text-danger"
                                                                />
                                                            </div>
                                                        </div>
                                                        <div className="mb-3 row">
                                                            <label className="col-sm-3 col-form-label col-form-label-sm text-primary">
                                                                Mt khu mi:
                                                            </label>
                                                            <div className="col-sm-9">
                                                                <Field
                                                                    type="password"
                                                                    className="form-control form-control-sm"
                                                                    name="new_password"
                                                                    placeholder="Mt khu mi"
                                                                />
                                                                <ErrorMessage
                                                                    component="div"
                                                                    name="new_password"
                                                                    className="text-danger"
                                                                />
                                                            </div>
                                                        </div>
                                                        <div className="mb-3 row">
                                                            <label className="col-sm-3 col-form-label col-form-label-sm text-primary">
                                                                Nhp li mt khu:
                                                            </label>
                                                            <div className="col-sm-9">
                                                                <Field
                                                                    type="password"
                                                                    className="form-control form-control-sm"
                                                                    name="repeat_password"
                                                                    placeholder="Nhp li mt khu"
                                                                />
                                                                <ErrorMessage
                                                                    component="div"
                                                                    name="repeat_password"
                                                                    className="text-danger"
                                                                />
                                                                <input
                                                                    type="hidden"
                                                                    className="form-control form-control-sm"
                                                                    name="type"
                                                                    value="PASSWORD"
                                                                />
                                                            </div>
                                                        </div>
                                                        <div className="row">
                                                            <div className="col-xl-12 col-xxl-6">
                                                                <div className="mb-2">
                                                                    <h4 className="card-title card-intro-title text-warning">
                                                                        Yu cu mt khu
                                                                    </h4>
                                                                </div>
                                                                <div className="card-content">
                                                                    <ul className="text-success">
                                                                        <li>Mt khu di 6 k t</li>
                                                                        <li>Phi bt buc l s</li>
                                                                        <li>Dng  xc minh chuyn khon</li>
                                                                    </ul>
                                                                </div>
                                                                <button type="submit" className="btn btn-success btn-sm mt-3">
                                                                    Cp Nht
                                                                </button>
                                                                <button
                                                                    type="button"
                                                                    className="btn btn-outline-info btn-sm mt-3 ms-2"
                                                                    onClick={(type) => handleShowModal("SECONDPASS")}
                                                                >
                                                                    Khi Phc
                                                                </button>
                                                            </div>
                                                        </div>
                                                    </Form>
                                                )}
                                            </Formik>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <ModalChangePassWord
                show={modalShow}
                onHide={handleCloseModal}
                type={restoreTpye}
            />
        </MasterLayout>
    );
}

export default UserChangePasswordPage;