import React, { useEffect, useState } from 'react';
import UserBankModel from '../../models/UserBankModel';
import Swal from "sweetalert2";
import AppNotification from '../../helpers/AppNotification';
import ModalCreateAcount from './ModalCreateAcount';
import UserBankAcountModel from '../../models/UserBankAcountModel';
import ModalDepositApp from './ModalDepositApp';
import Dropdown from 'react-bootstrap/Dropdown';
import ModalUpdateAcount from './ModalUpdateAcount';

function BankInfo(props) {

    // x l update acount
    const [showUpdateAccount, setShowUpdateAccount] = useState(false);
    const [dataAcountUpdate, setDataAcountUpdate] = useState({});
    const [flagUpdateAcount, setFlagUpdateAcount] = useState(false);
    const handleCloseModalUpdateAcount = () => {
        setDataAcountUpdate({});
        setShowUpdateAccount(false);
    }
    useEffect(() => {
        if (flagUpdateAcount == true) {
            setShowUpdateAccount(true);
            setFlagUpdateAcount(false);
        }
    }, [dataAcountUpdate, flagUpdateAcount]);
    const handleShowModalUpdateAcount = (item) => {
        setDataAcountUpdate(item);
        setFlagUpdateAcount(true);
    }
    // show modal
    const [modalShow, setModalShow] = React.useState(false);
    const [modalDepositApp, setModalDepositApp] = React.useState(false);
    const [idBankAccount, setIdBankAccount] = useState();
    const [flag, setFlag] = useState(false);
    // const [listUserBankAcount, setListUserBankAcount] = useState(props.listUserBankAcount);
    const toggleList = () => {
        console.log('toggleList');
        var list = document.getElementById("myList");
        list.style.display = (list.style.display === "none") ? "block" : "none";
    }
    var bankInfo = props.bankInfo;
    const [userBank, setUserbank] = useState({
        user_status: '',
        bank_id: '',
    });
    const [checkClick, setCheckClick] = useState(false);
    const changUserBankStatus = (number) => {
        setUserbank({
            user_status: number,
            bank_id: bankInfo.id,
        });
        setCheckClick(true);
    }
    useEffect(() => {
        if (flag === true) {
            setModalDepositApp(true);
            setFlag(false);
        }
    }, [idBankAccount, flag]);
    const handleDepositApp = (id) => {
        setIdBankAccount(id);
        setFlag(true);
    }
    const handleCloseDepositApp = (id) => {
        setModalDepositApp(false);
        setIdBankAccount('');
    }
    useEffect(() => {
        if (checkClick) {
            UserBankModel.updateStatus(userBank)
                .then((res) => {
                    // Notification
                    AppNotification.showSuccess('Cp nht thnh cng!');
                    Swal.fire({
                        icon: "success",
                        title: "Cp nht thnh cng!",
                        showConfirmButton: false,
                        timer: 2000,
                    });
                    setTimeout(() => {
                        props.reloadPage();
                    }, 2000);
                })
                .catch((err) => {
                    console.log(err);
                    AppNotification.showError('Cp nht tht bi!');
                    Swal.fire({
                        icon: "error",
                        title: "Cp nht tht bi!",
                        showConfirmButton: false,
                        timer: 2000,
                    });
                });
        }
    }, [userBank, checkClick]);

    const reloadPage = () => {
        window.location.reload();
    };

    return (
        <>
            <div className='row'>
                <div className='col-xl-12'>
                    <div className='card'>
                        <div className="card-header">
                            <h4 className="card-title">Thng tin ngn hng {bankInfo.name}</h4>
                        </div>
                        <div className='card-body'>
                            <div className="row pt-3">
                                <div className="col-4">
                                    <strong>Ngn Hng:</strong>
                                </div>
                                <div className="col-8">
                                    {bankInfo.name}
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col-4">
                                    <strong>Tnh trng:</strong>
                                </div>
                                <div className="col-8">
                                    {bankInfo.status === 1 ? (
                                        <span className="badge badge-success">Hot ng</span>
                                    ) : (
                                        <span className="badge badge-danger">ang Tt</span>
                                    )}
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col-4">
                                    <strong>ang S Dng:</strong>
                                </div>
                                <div className="col-8">
                                    {bankInfo.user_status === 1 ? (
                                        <span className="badge badge-success">Bt</span>
                                    ) : (
                                        <span className="badge badge-danger">Tt</span>
                                    )}
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col-4">
                                    <strong>Gi m ngn hng:</strong>
                                </div>
                                <div className="col-8">
                                    {bankInfo.opening_fee}  (Vnh Vin)
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col-4">
                                    <strong>Gi m ti khon:</strong>
                                </div>
                                <div className="col-8">
                                    100.000  (t ti khon th 2)
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col-4">
                                    <strong>Gi cp nht ti khon (sa,xa):</strong>
                                </div>
                                <div className="col-8">
                                    100.000 
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col-4">
                                    <strong className='text-info'>Gi np tin vo TK APP:</strong>
                                </div>
                                <div className="col-8 text-info">
                                    20.000  + 0.1% tng bill/lt
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col-4">
                                    <strong className='text-info'>Gi chuyn tin trong TK APP:</strong>
                                </div>
                                <div className="col-8 text-info">
                                    88.000  + 0.1% tng bill/lt
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col-4">
                                    <strong>Gi kim tra s ti khon t ng:</strong>
                                </div>
                                <div className="col-8">
                                    2.000  / lt
                                </div>
                            </div>

                            <div className="row pt-3">
                                <div className="col-4">
                                    <strong className="text-warning">Lu  quan trng:</strong>
                                </div>
                                <div className="col-8">
                                    <span className="text-warning">Dng s s in thoi ng k ngn hng  ng nhp vo App.</span> <br />
                                    <span className="text-warning">Khi np tin vo App, Tng nhn  App = s tin np x 100</span> <br />
                                    <span className="text-warning">V d: Np 10.000, Tng nhp  App = 10.000 x 100 = 1.000.000</span>
                                </div>
                            </div>
                            <div className='p-3'>
                                <div
                                    className="toolbar toolbar-bottom"
                                    role="toolbar"
                                    style={{ textAlign: "right" }}
                                >
                                    {
                                        bankInfo.user_status === 1 ?
                                            <button className="btn btn-danger sw-btn-next btn-sm" type="button" onClick={() => changUserBankStatus(0)}>
                                                ng Ngn Hng
                                            </button>
                                            :
                                            <button className="btn btn-success sw-btn-next btn-sm" type="button" onClick={() => changUserBankStatus(1)}>
                                                M Ngn Hng
                                            </button>
                                    }
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {bankInfo.user_status === 1 ? (
                <div className='row'>
                    <div className='col-xl-12'>
                        <div className='card'>
                            <div className="card-header">
                                <h4 className="card-title">Danh sch ti khon</h4>
                            </div>
                            <div className='card-body'>
                                <div id="example_wrapper" className="dataTables_wrapper">
                                    <div className='row'>
                                        <div className='col-6'>

                                        </div>
                                        <div className='col-6 pb-3'>
                                            <div
                                                className="toolbar toolbar-bottom"
                                                role="toolbar"
                                                style={{ textAlign: "right" }}
                                            >
                                                <button className="btn btn-primary sw-btn-next btn-sm" type="button" onClick={() => setModalShow(true)}>
                                                    Thm Ti Khon
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                    <div className='row d-flex pb-3' style={{ fontSize: '14px' }}>
                                        <div className='col-2'>
                                            <span className='text-warning'>S ti khon</span>
                                        </div>
                                        <div className='col-3'>
                                            <span className='text-warning'>Ch ti khon</span>
                                        </div>
                                        <div className='col-2'>
                                            <span className='text-warning'>S in thoi</span>
                                        </div>
                                        <div className='col-2'>
                                            <span className='text-warning'>Tnh Trng</span>
                                        </div>
                                        <div className='col-3'>
                                            <span className='text-warning'>Chi tit-Np tin</span>
                                        </div>
                                    </div>
                                    {
                                        props.listUserBankAcount.map((item, key) => (
                                            <div className='row pt-3' key={key} style={{ fontSize: '14px' }}>
                                                <div className='col-2 d-flex flex-column justify-content-evenly'>
                                                    <span className='text-primary'>{item.bank_number}</span>
                                                </div>
                                                <div className='col-3 d-flex flex-column justify-content-evenly'>
                                                    <span className='text-primary'>{item.bank_username}</span>
                                                </div>
                                                {/* <div className='col-2'>
                                                        {item && item.account_balance &&
                                                            <span className='text-primary'>{item.account_balance.toLocaleString('en-US')}</span>
                                                        }
                                                    </div> */}
                                                <div className='col-2 d-flex flex-column justify-content-evenly'>
                                                    <span className='text-primary'>{item.phone}</span>
                                                </div>
                                                <div className='col-2 d-flex flex-column justify-content-evenly'>
                                                    {item.status === 1 ? (
                                                        <span className="badge badge-success">Hot ng</span>
                                                    ) : (
                                                        <span className="badge badge-danger">ang Tt</span>
                                                    )}
                                                </div>
                                                <div className='col-3'>
                                                    <div className='row'>
                                                        <div className='col-12'>
                                                            <div className='row'>
                                                                <div className='col-6'>
                                                                    <Dropdown className=''>
                                                                        <Dropdown.Toggle className='btn btn-sm ms-2' variant="success" id="dropdown-basic">
                                                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-eye" viewBox="0 0 16 16">
                                                                                <path d="M16 8s-3-5.5-8-5.5S0 8 0 8s3 5.5 8 5.5S16 8 16 8M1.173 8a13 13 0 0 1 1.66-2.043C4.12 4.668 5.88 3.5 8 3.5s3.879 1.168 5.168 2.457A13 13 0 0 1 14.828 8q-.086.13-.195.288c-.335.48-.83 1.12-1.465 1.755C11.879 11.332 10.119 12.5 8 12.5s-3.879-1.168-5.168-2.457A13 13 0 0 1 1.172 8z" />
                                                                                <path d="M8 5.5a2.5 2.5 0 1 0 0 5 2.5 2.5 0 0 0 0-5M4.5 8a3.5 3.5 0 1 1 7 0 3.5 3.5 0 0 1-7 0" />
                                                                            </svg>
                                                                        </Dropdown.Toggle>
                                                                        <Dropdown.Menu>
                                                                            <Dropdown.Item href="#">S d : {item.account_balance_fm} VN </Dropdown.Item>
                                                                            <Dropdown.Item href="#">M 2FA : {item.password_level_two}</Dropdown.Item>
                                                                            <Dropdown.Item href="#">Mt khu : {item.password_user}</Dropdown.Item>
                                                                        </Dropdown.Menu>
                                                                    </Dropdown>
                                                                </div>
                                                                <div className='col-6'>
                                                                    <Dropdown >
                                                                        <Dropdown.Toggle className='btn btn-sm' variant="primary" id="dropdown-basic">
                                                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-plus-lg" viewBox="0 0 16 16">
                                                                                <path fill-rule="evenodd" d="M8 2a.5.5 0 0 1 .5.5v5h5a.5.5 0 0 1 0 1h-5v5a.5.5 0 0 1-1 0v-5h-5a.5.5 0 0 1 0-1h5v-5A.5.5 0 0 1 8 2" />
                                                                            </svg>
                                                                        </Dropdown.Toggle>
                                                                        <Dropdown.Menu>
                                                                            <Dropdown.Item className='text-primary' onClick={() => handleDepositApp(item.id)}>Np tin vo App</Dropdown.Item>
                                                                            <Dropdown.Item className='text-warning' onClick={() => handleShowModalUpdateAcount(item)}>Sa ti khon</Dropdown.Item>
                                                                        </Dropdown.Menu>
                                                                    </Dropdown>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        ))
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            ) : (
                ""
            )}
            <ModalCreateAcount
                show={modalShow}
                onHide={() => setModalShow(false)}
                bankName={bankInfo.name}
                BankStatus={bankInfo.status}
                UserBankStatus={bankInfo.user_status}
                bin={bankInfo.bin}
                type={bankInfo.type}
                reloadPage={reloadPage}
            />
            <ModalDepositApp
                show={modalDepositApp}
                onHide={() => setModalDepositApp(false)}
                bankName={bankInfo.name}
                BankStatus={bankInfo.status}
                UserBankStatus={bankInfo.user_status}
                bin={bankInfo.bin}
                type={bankInfo.type}
                user_bank_account_id={idBankAccount}
                reloadPage={reloadPage}
            />
            <ModalUpdateAcount
                show={showUpdateAccount}
                onHide={() => handleCloseModalUpdateAcount()}
                bin={bankInfo.bin}
                type={bankInfo.type}
                bankName={bankInfo.name}
                data={dataAcountUpdate}
                new={1}
            />
        </>
    );
}

export default BankInfo;