    import React, { useState } from 'react';
    import Modal from 'react-bootstrap/Modal';
    import { Formik, Field, Form, ErrorMessage } from 'formik';
    import * as Yup from 'yup';
    import VerifyCode from '../../models/VerifyCode';
    import AppNotification from '../../helpers/AppNotification';
    import UserModel from '../../models/UserModel';
    import PaymentModel from '../../models/PaymentModel';
    import Swal from 'sweetalert2';
import { useDispatch } from 'react-redux';
import { setShowPreloader } from '../../redux/action';
import CurrencyInput from '../global/CurrencyInput';

    const validationSchema = Yup.object().shape({
        bank_name: Yup.string().required("Vui lng chn ngn hng"),
        bank_number: Yup.string().required("Vui lng nhp s ti khon"),
        bank_user: Yup.string().required("Vui lng nhp ch ti khon"),
        amount: Yup.number()
            .required('Vui lng nhp s tin')
            .min(100000, 'S tin phi ln hn hoc bng 100.000'),
        verify_code: Yup.string()
            .required('Vui lng nhp m xc nhn')
            .matches(/^\d{6}$/, 'M xc nhn phi ng 6 ch s')
    });

    function ModalWithdraw(props) {
        const dispatch = useDispatch();

        const [form, setForm] = useState({
            bank_name: "",
            bank_number: "",
            bank_user: "",
            amount: 0,
            verify_code: ""
        });

        const handleSubmit = (values) => {
            PaymentModel.withdraw(values).then(function (res) {
                AppNotification.showSuccess(res.data);
                console.log(res.data);
                if (res.success == true) {
                    Swal.fire({
                        icon: "success",
                        title: "Yu cu rt tin c gi i thnh cng thnh cng!",
                        showConfirmButton: false,
                        timer: 2000,
                      });
                      props.onHide();
                      props.reloadPage();
                }else {
                    Swal.fire({
                        icon: "error",
                        title: res.data,
                        showConfirmButton: false,
                        timer: 2000,
                      });
                }
            }).catch(function (err) {
                console.log(err);
                AppNotification.showError('Cp Nht Mt Khu Tht Bi!');
            })

        };

        const sendOTP = () => {
            dispatch(setShowPreloader(true));
            VerifyCode.getOTP({
                type: "EARNMONEY"
            }).then(function (res) {
                dispatch(setShowPreloader(false));
                Swal.fire({
                    icon: "success",
                    title: "M OTP  c gi vo email ca bn!",
                    showConfirmButton: false,
                    timer: 2000,
                  });
                AppNotification.showSuccess('M OTP  c gi vo email ca bn!');
            }).catch(function (err) {
                Swal.fire({
                    icon: "error",
                    title: "Ly M OTP Tht Bi!",
                    showConfirmButton: false,
                    timer: 2000,
                  });
                AppNotification.showError('Ly M OTP Tht Bi!');
            })
        }

        return (
            <Modal
                {...props}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
            >
                <Modal.Header>
                    <Modal.Title id="contained-modal-title-vcenter">
                        Rt tin v ngn hng
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Formik
                        initialValues={form}
                        validationSchema={validationSchema}
                        onSubmit={handleSubmit}
                    >
                        {({ errors, touched, setFieldValue }) => (
                            <Form>
                                {/* <div style={{ marginBottom: "20px" }}>
                                    <strong style={{ fontSize: "1.2em" }}>
                                        S tin c th rt: 0
                                    </strong>
                                </div> */}

                                <div className="mb-3">
                                    <label htmlFor="bank_name" className="form-label">
                                        Ngn hng (*)
                                    </label>
                                    <Field
                                        as="select"
                                        id="bank_name"
                                        name="bank_name"
                                        className={`form-select ${errors.bank_name && touched.bank_name ? "is-invalid" : ""
                                            }`}
                                        style={{
                                            backgroundColor: "#000936",
                                            border: "0.0625rem solid rgba(255, 255, 255, 0.25)"
                                        }}
                                    >
                                        <option value="">Chn ngn hng</option>
                                        <option value="vietcombank">Vietcombank</option>
                                        <option value="acb">ACB</option>
                                        <option value="techcombank">Techcombank</option>
                                    </Field>
                                    {errors.bank && touched.bank && (
                                        <div className="invalid-feedback">{errors.bank}</div>
                                    )}
                                </div>

                                <div className="mb-3">
                                    <label htmlFor="bank_number" className="form-label">
                                        S ti khon (*)
                                    </label>
                                    <Field
                                        type="text"
                                        id="bank_number"
                                        name="bank_number"
                                        className={`form-control ${errors.bank_number && touched.bank_number ? "is-invalid" : ""
                                            }`}
                                        autoFocus
                                    />
                                    {errors.bank_number && touched.bank_number && (
                                        <div className="invalid-feedback">{errors.bank_number}</div>
                                    )}
                                </div>

                                <div className="mb-3">
                                    <label htmlFor="accountHolder" className="form-label">
                                        Ch ti khon (*)
                                    </label>
                                    <Field
                                        type="text"
                                        id="bank_user"
                                        name="bank_user"
                                        className={`form-control ${errors.bank_user && touched.bank_user ? "is-invalid" : ""
                                            }`}
                                        autoFocus
                                    />
                                    {errors.bank_user && touched.bank_user && (
                                        <div className="invalid-feedback">{errors.bank_user}</div>
                                    )}
                                </div>

                                <div className="mb-3">
                                    <label htmlFor="amount" className="form-label">
                                        S tin (*)
                                    </label>
                                    <CurrencyInput
                                        type="text"
                                        className={`form-control ${errors.amount && touched.amount ? 'is-invalid' : ''
                                            }`}
                                        autoFocus
                                        onChange={e => {
                                            let value = e.target.value || "";
                                            value = value.replaceAll(',','');
                                            setFieldValue("amount", value);
                                            }}
                                    />
                                    <Field
                                        type="hidden"
                                        id="amount"
                                        name="amount"
                                        className={`form-control ${errors.amount && touched.amount ? "is-invalid" : ""
                                            }`}
                                        autoFocus
                                    />
                                    {errors.amount && touched.amount && (
                                        <div className="invalid-feedback">{errors.amount}</div>
                                    )}
                                </div>

                                <div className="mb-3">
                                    <label htmlFor="verify_code" className="form-label">
                                        Xc nhn (*)
                                    </label>
                                    <div className="input-group">
                                        <Field
                                            type="text"
                                            id="verify_code"
                                            name="verify_code"
                                            className={`form-control ${errors.verify_code && touched.verify_code ? "is-invalid" : ""
                                                }`}
                                            autoFocus
                                        />
                                        <button
                                            type="button"
                                            className="btn btn-outline-warning"
                                            onClick={sendOTP}
                                        >
                                            Ly m
                                        </button>
                                        {errors.verify_code && touched.verify_code && (
                                            <div className="invalid-feedback">{errors.verify_code}</div>
                                        )}
                                    </div>
                                </div>

                                <div className="modal-footer">
                                    <button className="btn btn-danger" type="button" onClick={props.onHide}>
                                        Close
                                    </button>
                                    <button className="btn btn-success" type="submit">
                                        Xc nhn
                                    </button>
                                </div>
                            </Form>
                        )}
                    </Formik>
                    {/* <p>Lu :</p>
                    <ul>
                        <li>
                            Nhn <span className="text-warning">ly OTP</span>, mt m xc minh c gi vo email bn ng k
                        </li>
                        <li>
                            Ly m ri nhp m vo <span className="text-warning">Nhp m xc thc...</span>
                        </li>
                        <li>Nhn Khi phc  hon tt!</li>
                        <li className="text-success">Nu khng thy mail OTP, vui lng c trong tin nhn SPAM!</li>
                    </ul> */}
                </Modal.Body>
                {/* <Modal.Footer>
                    <Button onClick={props.onHide}>ng</Button>
                </Modal.Footer> */}
            </Modal>
        );
    }

    export default ModalWithdraw;