import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import VerifyCode from '../../models/VerifyCode';
import AppNotification from '../../helpers/AppNotification';
import UserModel from '../../models/UserModel';
import Swal from "sweetalert2";
import ModalNoticationPass from './ModalNoticationPass';

const validationSchema = Yup.object().shape({
  verify_code: Yup.string()
    .required('Vui lng nhp m xc thc')
    .matches(/^\d{6}$/, 'M xc thc phi c ng 6 ch s')
});

function ModalChangePassWord(props) {
  const [modalShowNoti, setModalShowNoti] = React.useState(false);
  const [notication,setNotication] = useState();
  const handleCloseModalNoti = () => {
      setModalShowNoti(false);
  };
  const handleShowModalNoti = () => {
    setModalShowNoti(true);
  };
  const [formPass, setFormPass] = useState({
    verify_code: "",
    type: "PASSWORD",
  });
  const [formSecondpass, setFormSecondpass] = useState({
    verify_code: "",
    type: "SECONDPASS",
  });
  const handleSubmit = (values) => {
      console.log(values);
      UserModel.restore(values).then(function (res) {
          if (res.success == true) {
            setNotication(res.data);
            props.onHide();
            Swal.fire({
              icon: "success",
              title: "mt khu c khi phc!",
              showConfirmButton: false,
              timer: 2000,
            });
            setModalShowNoti(true);
          }else {
            Swal.fire({
              icon: "error",
              title: res.data,
              showConfirmButton: false,
              timer: 2000,
            });
          }
          AppNotification.showSuccess(res.data);
      }).catch(function (err) {
          console.log(err);
          AppNotification.showError('Khi Phc Mt Khu Tht Bi!');
      })
  };

  const sendOTP = () => {
      VerifyCode.getOTP({
        type: props.type
      }).then(function (res) {
        Swal.fire({
          icon: "success",
          title: "M OTP  c gi vo email ca bn!",
          showConfirmButton: false,
          timer: 2000,
        });
          AppNotification.showSuccess('M OTP  c gi vo email ca bn!');
      }).catch(function (err) {
        Swal.fire({
          icon: "error",
          title: "Khng gi c m OTP!",
          showConfirmButton: false,
          timer: 2000,
        });
          console.log(err);
          AppNotification.showError('Ly M OTP Tht Bi!');
      })
  }

  const initialValues = props.type === "PASSWORD" ? formPass : formSecondpass;
  return (
    <>
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header>
          <Modal.Title id="contained-modal-title-vcenter">
            {props.type === 'PASSWORD' ? 'Xc minh i mt khu' : 'Xc minh i mt khu Cp 2'}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <h4>Nhp m xc thc  i mt khu.</h4>
          <Formik
            initialValues={initialValues}
            validationSchema={validationSchema}
            onSubmit={handleSubmit}
          >
            <Form>
              <div className="input-group mb-2">
                <Field type="text" className="form-control" name="verify_code" />
                <button className="btn btn-warning" type="button" onClick={sendOTP}>
                  Ly m
                </button>
                <button className="btn btn-primary" type="submit">
                  Khi phc
                </button>
              </div>
              <ErrorMessage
                component="div"
                name="verify_code"
                className="text-danger"
              />
            </Form>
          </Formik>
          <p>Lu :</p>
          <ul>
            <li>
              Nhn <span className="text-warning">ly OTP</span>, mt m xc minh c gi vo email bn ng k
            </li>
            <li>
              Ly m ri nhp m vo <span className="text-warning">Nhp m xc thc...</span>
            </li>
            <li>Nhn Khi phc  hon tt!</li>
            <li className="text-success">Nu khng thy mail OTP, vui lng c trong tin nhn SPAM!</li>
          </ul>
        </Modal.Body>
        <Modal.Footer>
          <Button onClick={props.onHide}>ng</Button>
        </Modal.Footer>
      </Modal>
      <ModalNoticationPass show={modalShowNoti} onHide={handleCloseModalNoti} notication={notication}/>
    </>
  );
}

export default ModalChangePassWord;